






























































import Vue from "vue";
// import Component from "vue-class-component";
import dayjs from "dayjs";
import { Component, Prop, Watch } from "vue-property-decorator";
import DatePicker from "@/components/DatePicker.vue";
import { Toast } from "vant";
import Dialog from "@/components/Dialog.vue";
Vue.use(Toast);
Toast.setDefaultOptions({ duration: 1000 });
@Component({
  components: {
    DatePicker,
    Dialog,
  },
})
export default class extends Vue {
  @Prop(String) type!: string;
  @Prop(String) tag!: String;
  output: string = "0.00";
  flag: String = "ok";
  remark: string = "";
  date: Date = new Date();
  derectFlag: boolean = false;
  show: boolean = false;

  updateDate(value: Date) {
    this.date = value;
    console.log(dayjs(value).endOf("date"));
  }
  get formatDate() {
    if (
      dayjs(new Date()).format("M月D日") === dayjs(this.date).format("M月D日")
    ) {
      return "今天";
    }
    return dayjs(this.date).format("M月D日");
  }

  input(event: MouseEvent) {
    if (this.output.length >= 8) {
      return;
    }
    if (event.target) {
      const num = (event.target as HTMLButtonElement).textContent!;
      if (this.output === "0.00" || this.output === "0") {
        if (num === ".") {
          this.output = 0 + ".";
        } else {
          this.output = num;
        }
      } else {
        if (num === ".") {
          if (this.output.match(/[+-]/)) {
            if (this.output.match(/[+-]$/)) {
              this.output += "0.";
            } else {
              this.output += ".";
            }
          } else {
            if (this.output.indexOf(".") < 0) {
              this.output += ".";
            }
          }
        } else {
          this.output += num;
        }
      }
    }
  }
  math(type: string) {
    const reg1 = new RegExp("[" + type + "]$");
    let reg2 = type === "+" ? /[-]$/ : /[+]$/;

    if (
      this.output.length >= 8 ||
      this.output === "0.00" ||
      this.output === "0" ||
      this.output.match(reg1)
    ) {
      return;
    }
    if (this.output.match(/\d[+-]\d/)) {
      this.computed();
    } else if (this.output.match(/[.]$/)) {
      this.output += "0";
    } else if (this.output.match(reg2)) {
      this.output = this.output.slice(0, -1);
    }
    this.output += type;
  }
  back() {
    if (this.output === "0.00") {
      return;
    }
    if (this.output.length === 1) {
      this.output = "0";
      return;
    }
    this.output = this.output.slice(0, -1);
  }

  showPopup() {
    (this.$refs.DatePicker as DatePicker).showPopup();
  }
  showDiaLog() {
    (this.$refs.dialog as Dialog).showDiaLog();
  }
  callCancel() {
    this.remark = "";
  }
  textPut() {
    const value = window.prompt("请输入备注", this.remark);
    if (value) {
      this.remark = value;
    } else {
      this.remark = "";
    }
  }
  computed() {
    let str = this.output.indexOf("+") >= 0 ? "+" : "-";

    const arr: String[] = this.output.split(str);
    arr[1] =
      parseFloat(arr[1].toString()) === 0 ? "0" : arr[1].replace(/^0+/, "");
    1;
    this.output = arr.join(str);
    this.output = eval(this.output.toString()).toString();
  }
  computeds() {
    this.computed();
    this.flag = "ok";
  }
  ok() {
    if (this.output.match(/[+-.]$/)) {
      this.output = this.output.slice(0, -1);
    }
    if (this.output === "0" || this.output === "0.00") {
      return;
    }
    const record = {
      type: this.type,
      remark: this.remark,
      amount: parseFloat(parseFloat(this.output).toFixed(2)),
      tag: this.tag,
      date: this.date,
    };
    this.$store.commit("recordListPush", record);

    this.derectFlag = true;
    Toast.success("已记一笔");
    this.$router.replace("/detail");
  }

  @Watch("output")
  onOutputChange(value: String) {
    if (value.match(/\d[+-]\d/)) {
      this.flag = "=";
    } else {
      this.flag = "ok";
    }
  }
}
