













































import { Component, Vue, Watch } from "vue-property-decorator";
import NumPad from "@/components/NumPad.vue";

@Component({
  components: {
    NumPad,
  },
  beforeRouteLeave(to: any, from: any, next: any) {
    //判断是否缓存detail页面
    if (to.path === "/detail" && (this.$refs.numPad as NumPad).derectFlag) {
      to.meta.keepAlive = false;
    }
    next();
  },
})
export default class BoardHeader extends Vue {
  type: string = "支出";
  currindex: Number = -1;
  currItem: String = "";

  select(index: number, item: String) {
    this.currindex = index;
    this.currItem = item;
  }
  changeType(type: string) {
    this.type = type;
    this.currindex = -1;
    this.currItem = "";
  }
  get tags() {
    return this.type === "支出"
      ? this.$store.state.tagsOut
      : this.$store.state.tagsIn;
  }
  edit() {
    this.$router.push({
      path: "book/edit",
      query: {
        type: this.type,
      },
    });
  }
}
